
      @use 'sass:math';
      @use 'sass:color';
      @import '/Users/nickmajor/projects/rw/care-ops-frontend/src/scss/provider-variables.scss';
    
.program-action--draft {
  color: $black-20;
  font-weight: bold;
}

.program-action--automated,
.program-action--standard,
.program-action--conditional {
  color: color(green);
  font-weight: bold;
}

.is-highlighted {
  .program-action--draft,
  .program-action--automated,
  .program-action--conditional,
  .program-action--standard {
    color: $white;
  }
}


.is-disabled {
  .program-action--automated,
  .program-action--standard {
    color: $black-80;
  }
}
